<template>
    <section>
        <div class="modal fade dialog my-popups ui-draggable show"
            style="height: auto; width: auto; display: block; top: 0px; left: 0px;" data-keyboard="false"
            data-backdrop="static" role="dialog">
            <div role="document" class="modal-dialog maxwidth500">
                <div class="modal-content">
                    <div class="modal-header">

                        <h5 id="myModalLabel" class="modal-title text-truncate pb-1">{{ $t('CopyTimesheet') }}</h5>
                        <button type="button" data-dismiss="modal" class="close" v-on:click="closePopup"><i
                                aria-hidden="true" class="fa fa-times"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left" id="CopyTimesheet">
                                <div class="col-12 p-0 mb-2 d-flex">
                                    <span class="mble_show">{{ $t('SelectUserFrom') }} : </span>
                                    <div class="ml-3 custom-control custom-radio custom-control-inline">
                                        <input type="radio" value="Department" v-model="Type"
                                            class="form-check-input custom-control-input" id="Department"
                                            name="Department"><label class="custom-control-label" for="Department">{{
                            $t('Department') }}</label>

                                    </div>

                                    <div class="custom-control custom-radio custom-control-inline">
                                        <input type="radio" value="User" v-model="Type"
                                            class="form-check-input  custom-control-input" id="User" name="User"><label
                                            class="custom-control-label" for="User">{{ $t('Users') }}</label>
                                    </div>
                                    <div class="custom-control custom-radio custom-control-inline">
                                        <input type="radio" value="TimerProject" v-model="Type"
                                            class="form-check-input  custom-control-input" id="Project"
                                            name="project"><label class="custom-control-label" for="Project">{{
                            $t('Project') }}</label>
                                    </div>
                                    <div class="custom-control custom-radio custom-control-inline">
                                        <input type="radio" value="Location" v-model="Type"
                                            class="form-check-input  custom-control-input" id="Location"
                                            name="Location"><label class="custom-control-label" for="Location">{{
                            $t('Location') }}</label>
                                    </div>

                                </div>
                            </div>
                        </div>

                        <div class="row">
                            <div class="col-lg-6" v-if="Type != 'User' && Type != 'Location'">
                                <div class="form-group" v-if="IsShowDiv">

                                    <label>{{ LabelName }} <span class="text-danger">*</span></label>
                                    <select v-model="ParentValue" v-on:change="UsersByParentId(ParentValue)"
                                        v-bind:class="{ 'form-control': true }">
                                        <option value="" disabled selected="Selected">{{ $t('Select') }}</option>
                                        <option v-for="item, index in UserParentList" v-bind:value="item.Id"
                                            :key="index">
                                            {{ item.Name }}
                                        </option>
                                    </select>

                                </div>

                            </div>
                            <div class="col-lg-6" v-if="Type == 'Location'">
                                <div class="form-group" v-if="IsShowDiv">
                                    <label>{{ LabelName }} <span class="text-danger">*</span></label>
                                    <Treeselect v-model="ParentValue" 
                                        class="form-control p-0 multiselect"
                                        :multiple="false" :options="FindalLocationArray" placeholder="Select" :normalizer="normalizer"
                                        @input="UsersByParentId(ParentValue)">
                                    </Treeselect>

                                </div>

                            </div>
                            <div class="col-lg-6" v-if="chkValidate == true">
                                <div class="form-group">
                                    <label>Users: <span class="text-danger" v-if="IsRequiredDiv">*</span></label>
                                    <Multiselect label="label" track-by="value" v-model="SeletcedValue"
                                        :options="Options" mode="tag" :close-on-select="false" :searchable="true"
                                        :multiple="true" :taggable="false" :placeholder="$t('Select')">
                                        <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                    </Multiselect>

                                </div>
                            </div>
                            <div class="col-lg-6" v-if="chkValidate == false">
                                <div class="form-group">
                                    <label>Users: <span class="text-danger" v-if="IsRequiredDiv">*</span></label>

                                    <Multiselect label="label" track-by="value" v-model="SeletcedValue"
                                        :options="Options" mode="tag" :close-on-select="false" :searchable="true"
                                        :multiple="true" :taggable="false" :placeholder="$t('Select')">
                                        <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                    </Multiselect>

                                </div>
                            </div>
                        </div>
                        <div class="modal-footer px-0">
                            <div class="row flex-row-reverse w-100">

                                <div class="col-lg-6 text-right p-0">
                                    <button @click="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{
                                        $t('Save') }}</button>
                                    <button @click="closePopup" type="button" class="btn btn-danger"
                                        style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel')
                                        }}</button>
                                </div>


                                <div class="col-lg-6 p-0">
                                    <div class="py-2"><span class="mandatory">{{ $t('MandatoryString') }}</span></div>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>



    </section>
</template>
<script>
import DataService from "../../../services/DataService"
import Treeselect from '@riophae/vue-treeselect'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default
    {

        data() {
            return {
                IsShowDiv: false,
                Type: 'User',
                LabelName: 'Users',
                IsRequiredDiv: true,
                ParentValue: '',
                UserParentList: [],
                Options: [],
                chkValidate: false,
                SeletcedValue: [],
                users: '',
                FindalLocationArray:[]
            }
        },
        components: {

            Treeselect
        },
        created: function () {
            //console.log("timeheet_userId",this.$parent.timeheetselecteduserId);
            this.BindUsersDDL('Users');

        },
        watch: {
            Type: function (val) {
                var vm = this;
                vm.onChange();
            }
        },
        methods: {
            closePopup() {
                $("body").removeClass("modal-open");
                this.$parent.openModal = false;
            },
            submit() {
                this.users = '';
                this.SeletcedValue.forEach((item, index) => {
                    // this.users.push(item.value)
                    this.users += index != 0 ? ',' + item.value : item.value

                })
                this.$parent.CopyToUserWeekTimesheet(this.users);
            },
            onChange: function () {
                var vm = this;
                this.SeletcedValue = [];
                this.ParentValue = '';
                if (vm.Type == "Department") {
                    this.BindDepartmentDDL('Department');
                    this.IsShowDiv = true;
                    this.IsRequiredDiv = true;
                    this.LabelName = "Department";
                    this.chkValidate = false;

                }
                else if (vm.Type == "User") {
                    this.BindUsersDDL('Users');
                    this.IsShowDiv = false;
                    this.IsRequiredDiv = true;
                    this.LabelName = "Users";
                    this.chkValidate = true;


                }
                else if (vm.Type == "TimerProject") {

                    this.BindDepartmentDDL('TimerProject');
                    this.IsShowDiv = true;
                    this.IsRequiredDiv = true;
                    this.LabelName = "Project";
                    this.chkValidate = false;

                }
                else if (vm.Type == "Location") {
                    this.ParentValue = null;
                    this.BindDepartmentDDL('Location');
                    this.LabelName = "Location";
                    this.IsShowDiv = true;
                    this.IsRequiredDiv = true;
                    this.chkValidate = false;

                }
            },
            UsersByParentId(ParentValue) {
                debugger
                var vm = this;

                if (vm.LabelName == "Department") {
                    vm.BindUsersDDL('Department', vm.ParentValue);
                }
                if (vm.LabelName == "Project") {
                    vm.BindUsersDDL('Project', vm.ParentValue);
                }
                if (vm.LabelName == "Users") {
                    vm.BindUsersDDL('Users', vm.ParentValue);
                }
                if (vm.LabelName == "Location") {
                    vm.BindUsersDDL('Location', vm.ParentValue);
                }

            },

            BindUsersDDL: function (refid, ParentValue) {
                var vm = this;
                vm.Options = []
                // Params for api hit
                let params = {};
                params["refCode"] = refid,
                    params["parentId"] = ParentValue,
                    params["timeheet_userId"] = this.$parent.timeheetselecteduserId
                DataService.SharedUsersParentListByParentId(params).then(function (response) {
                    vm.UserListData = JSON.parse(response.data);
                    vm.UserList = vm.UserListData.DocParentUserListing;
                    if (vm.UserList.length != null) {
                        vm.UserList.forEach((item, index) => {
                            vm.Value = item.Id;
                            vm.first_value = item.Name
                            vm.Options.push({
                                value: vm.Value,
                                label: vm.first_value
                            })
                        });
                        if (vm.GroupData != null) {
                            vm.BindGroupsData(vm.GroupData);
                            vm.IsBindEdited = false;
                        }
                    }
                });

            },
            // Bind department list on click of radiobuttion of department
            BindDepartmentDDL: function (refid, ParentValue) {
                var vm = this;
                vm.isLoading = true;
                vm.Options = []
                // Params for api hit
                let params = {};
                params["refCode"] = refid
                params["parentId"] = ParentValue

                // var params =`refCode=${vm.refid}`
                DataService.SharedUsersParentList(params).then(function (response) {
                    if (response.data != null) {
                        vm.UserListData = JSON.parse(response.data);
                        if (refid == 'Location') {
                           
                            // vm.UserListData=vm.buildHierarchy(vm.UserListData.DocParentListing)
                            vm.UserListData = vm.UserListData.DocParentListing;

                            vm.FindalLocationArray = [];
                            vm.UserListData.forEach((value) => {
                                var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                                });
                                if (CheckIfExist.length == 0) {
                                    if (value.PARENT_LOCATION_ID == null) {
                                        let locationId = value.COMPANY_LOCATION_ID
                                        let rslt = vm.getChildLocation(locationId, value, vm.UserListData)
                                        vm.FindalLocationArray.push(rslt)
                                    }
                                }
                            });
                        }
                        else {

                            vm.UserParentList = vm.UserListData.DocParentListing;
                        }
                    }
                    else {
                        vm.UserParentList = [];
                    }
                    vm.isLoading = false;
                });

            },
            //Bind Project list ddl
            BindUsersDDL: function (refid, ParentValue) {
                var vm = this;
                vm.Options = []
                // Params for api hit
                let params = {};
                params["refCode"] = refid,
                    params["parentId"] = ParentValue,
                    params["timeheet_userId"] = this.$parent.timeheetselecteduserId

                DataService.SharedUsersParentListByParentId(params).then(function (response) {
                    vm.UserListData = JSON.parse(response.data);
                    vm.UserList = vm.UserListData.DocParentUserListing;
                    if (vm.UserList.length != null) {
                        vm.UserList.forEach((item, index) => {
                            vm.Value = item.Id;
                            vm.first_value = item.Name
                            vm.Options.push({
                                value: vm.Value,
                                label: vm.first_value
                            })
                        });
                        if (vm.GroupData != null) {
                            vm.BindGroupsData(vm.GroupData);
                            vm.IsBindEdited = false;
                        }
                    }
                });

            },
            getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },
        //it is for when children is zero than remove no sub options in trreeselect.
        normalizer(node) {   return {   children: node.children && node.children.length ? node.children : 0   }   }
            // buildHierarchy(items) {

            //     const map = {};
            //     const root = [];

            //     // Create a mapping of items by their value
            //     items.forEach(item => {
            //         map[item.Id] = item;
            //     });

            //     console.log('items',items);
            //     // Iterate over items to build the hierarchy
            //     items.forEach(item => {
            //         item.id = item.Id;
            //         item.label = item.Name;
            //         const parentValue = item.parentLocId;
            //         // If the item has a parent, add it to the parent's children array
            //         if (parentValue != 0) {
            //             const parent = map[parentValue];
            //             if (parent) {
            //                 console.log('parent',parent);
            //                 parent.children = parent.children || [];
            //                 parent.children.push(item);
            //             }
            //         } else {
            //             // If the item has no parent, add it to the root array
            //             root.push(item);
            //         }
            //         console.log("root",root)
            //     });
            //     return root;
            // },

        }
    }


</script>

<style>
.multiselect__tags {
    min-height: calc(2.12rem + 2px) !important;
}

.multiselect__placeholder {
    font-weight: normal;
    padding-top: 4px;
}

.multiselect__input {
    padding: 0px !important;
}
</style>